.container {
  width: 100%;
  height: auto;
  display: flex;
  min-height: 100vh;
  align-items: center;
  flex-direction: column;
  justify-content: flex-start;
}
.navbar-container {
  width: 100%;
  display: flex;
  z-index: 100;
  position: fixed;
  box-shadow: 5px 5px 10px 0px #d4d4d4;
  align-items: center;
  flex-direction: column;
  justify-content: center;
  background-color: var(--dl-color-gray-white);
}
.navbar {
  width: 100%;
  display: flex;
  max-width: var(--dl-size-size-maxcontent);
  align-items: center;
  padding-top: var(--dl-space-space-tripleunit);
  padding-left: var(--dl-space-space-tripleunit);
  padding-right: var(--dl-space-space-tripleunit);
  flex-direction: row;
  padding-bottom: var(--dl-space-space-tripleunit);
  justify-content: space-between;
}
.navlink {
  display: contents;
}
.image {
  width: 30%;
  height: 60%;
  object-fit: cover;
  transition: 0.3s;
  text-decoration: none;
}
.image:hover {
  filter: contrast(150%);
}
.links-container {
  width: 40%;
  display: flex;
  align-items: flex-start;
  flex-direction: row;
}
.link {
  transition: 0.3s;
  margin-right: var(--dl-space-space-tripleunit);
  text-decoration: none;
}
.link:hover {
  color: var(--dl-color-gray-700);
}
.burger-menu {
  display: none;
  align-items: flex-start;
  flex-direction: column;
}
.icon {
  width: 36px;
  height: 36px;
}
.section-separator {
  width: 100%;
  height: 1px;
  display: flex;
  margin-top: var(--dl-space-space-tenunits);
  align-items: flex-start;
  flex-direction: column;
  background-color: var(--dl-color-gray-900);
}
.section-separator1 {
  width: 100%;
  height: 1px;
  display: flex;
  align-items: flex-start;
  flex-direction: column;
  background-color: var(--dl-color-gray-900);
}
.container1 {
  width: 100%;
  display: flex;
  align-items: center;
  flex-direction: column;
  justify-content: center;
  background-color: #f1f1f1;
}
.Hero {
  width: 100%;
  display: flex;
  padding: 32px;
  max-width: 1400px;
  min-height: 80vh;
  align-items: center;
  flex-direction: row;
  justify-content: space-between;
}
.image1 {
  width: 25rem;
  object-fit: cover;
}
.container2 {
  margin-top: var(--dl-space-space-tripleunit);
  display: flex;
  margin-left: 32px;
  flex-direction: column;
}
.text {
  font-size: 52px;
}
.text01 {
  margin-top: 32px;
  margin-bottom: 32px;
  padding-right: 48px;
}
.text12 {
  margin-top: var(--dl-space-space-unit);
  font-weight: 600;
  margin-bottom: var(--dl-space-space-unit);
}
.text13 {
  margin-bottom: 32px;
  padding-right: 48px;
}
.section-separator2 {
  width: 100%;
  height: 1px;
  display: flex;
  align-items: flex-start;
  flex-direction: column;
  background-color: var(--dl-color-gray-900);
}
.footer-container {
  width: 100%;
  display: flex;
  align-items: center;
  flex-direction: column;
  justify-content: center;
  background-color: var(--dl-color-gray-white);
}
.footer {
  width: 100%;
  display: flex;
  max-width: var(--dl-size-size-maxcontent);
  align-items: center;
  padding-top: var(--dl-space-space-tripleunit);
  padding-left: var(--dl-space-space-tripleunit);
  padding-right: var(--dl-space-space-tripleunit);
  flex-direction: row;
  padding-bottom: var(--dl-space-space-tripleunit);
  justify-content: space-between;
}
.social-links {
  display: flex;
  align-items: flex-start;
  flex-direction: row;
}
.icon02 {
  width: 24px;
  height: 24px;
  margin-right: var(--dl-space-space-doubleunit);
}
.icon04 {
  width: 24px;
  height: 24px;
  margin-right: var(--dl-space-space-doubleunit);
}
.icon06 {
  width: 24px;
  height: 24px;
}
.copyright-container {
  display: flex;
  align-items: center;
  flex-direction: row;
}
.icon08 {
  width: 24px;
  height: 24px;
  margin-right: var(--dl-space-space-doubleunit);
}
@media (max-width: 991px) {
  .image {
    width: 30%;
  }
  .links-container {
    width: 60%;
  }
  .Hero {
    padding: 48px;
    flex-direction: column;
  }
  .image1 {
    order: 2;
  }
  .container2 {
    align-items: center;
    margin-left: 0px;
    margin-right: 0px;
  }
  .text {
    font-size: 52px;
    text-align: center;
  }
  .text01 {
    text-align: center;
    padding-left: var(--dl-space-space-fourunits);
  }
  .text12 {
    text-align: center;
  }
  .text13 {
    text-align: center;
    padding-left: var(--dl-space-space-fourunits);
  }
}
@media (max-width: 767px) {
  .image {
    width: 45%;
  }
  .links-container {
    display: none;
  }
  .burger-menu {
    display: flex;
    padding: var(--dl-space-space-halfunit);
    margin-left: var(--dl-space-space-tripleunit);
    border-radius: var(--dl-radius-radius-radius4);
    background-color: var(--dl-color-gray-black);
  }
  .icon {
    fill: var(--dl-color-gray-white);
  }
  .Hero {
    padding-left: 32px;
    padding-right: 32px;
  }
  .image1 {
    width: 80%;
  }
  .text {
    font-size: 42px;
    text-align: center;
  }
}
@media (max-width: 479px) {
  .image {
    width: 70%;
  }
  .burger-menu {
    margin-left: var(--dl-space-space-doubleunit);
  }
  .Hero {
    padding-top: 32px;
    padding-left: var(--dl-space-space-unit);
    padding-right: var(--dl-space-space-unit);
    padding-bottom: 32px;
  }
  .container2 {
    margin-bottom: var(--dl-space-space-unit);
  }
  .text {
    margin-top: var(--dl-space-space-doubleunit);
    font-size: 32px;
    text-align: center;
  }
  .text01 {
    margin-top: var(--dl-space-space-tripleunit);
    padding-left: var(--dl-space-space-unit);
    padding-right: var(--dl-space-space-unit);
  }
  .text13 {
    padding-left: var(--dl-space-space-unit);
    padding-right: var(--dl-space-space-unit);
  }
  .link4 {
    font-size: 12px;
  }
}
