.container {
  width: 100%;
  height: auto;
  display: flex;
  min-height: 100vh;
  align-items: center;
  flex-direction: column;
}
.navbar-container {
  width: 100%;
  display: flex;
  z-index: 100;
  position: fixed;
  box-shadow: 5px 5px 10px 0px #d4d4d4;
  align-items: center;
  flex-direction: column;
  justify-content: center;
  background-color: var(--dl-color-gray-white);
}
.navbar {
  width: 100%;
  display: flex;
  max-width: var(--dl-size-size-maxcontent);
  align-items: center;
  padding-top: var(--dl-space-space-tripleunit);
  padding-left: var(--dl-space-space-tripleunit);
  padding-right: var(--dl-space-space-tripleunit);
  flex-direction: row;
  padding-bottom: var(--dl-space-space-tripleunit);
  justify-content: space-between;
}
.navlink {
  display: contents;
}
.image {
  width: 30%;
  height: 60%;
  object-fit: cover;
  transition: 0.3s;
  text-decoration: none;
}
.image:hover {
  filter: contrast(150%);
}
.links-container {
  width: 40%;
  display: flex;
  align-items: space-between;
  flex-direction: row;
}
.link {
  transition: 0.3s;
  margin-right: var(--dl-space-space-tripleunit);
  text-decoration: none;
}
.link:hover {
  color: var(--dl-color-gray-700);
}
.burger-menu {
  display: none;
  align-items: flex-start;
  flex-direction: column;
}
.icon {
  width: 36px;
  height: 36px;
}
.section-separator {
  width: 100%;
  height: 1px;
  display: flex;
  margin-top: var(--dl-space-space-tenunits);
  align-items: flex-start;
  flex-direction: column;
  background-color: var(--dl-color-gray-900);
}
.section-separator1 {
  width: 100%;
  height: 1px;
  display: flex;
  align-items: flex-start;
  flex-direction: column;
  background-color: var(--dl-color-gray-900);
}
.Oferta {
  width: 100%;
  display: flex;
  max-width: 1400px;
  align-items: center;
  padding-top: 70px;
  padding-left: 48px;
  padding-right: 48px;
  flex-direction: column;
  padding-bottom: 48px;
  justify-content: space-between;
}
.container01 {
  width: 100%;
  display: flex;
  max-width: 1400px;
  align-self: center;
  align-items: stretch;
  margin-bottom: 32px;
  flex-direction: row;
  justify-content: space-between;
}
.image1 {
  width: 40%;
  object-fit: cover;
  border-radius: var(--dl-radius-radius-radius8);
}
.container02 {
  flex: 1;
  display: flex;
  padding: 16px;
  margin-left: 10px;
  align-items: flex-start;
  flex-direction: column;
  justify-content: center;
}
.text {
  color: var(--dl-color-gray-500);
  font-style: normal;
  font-weight: 600;
  margin-bottom: 24px;
  text-transform: uppercase;
}
.text02 {
  font-size: 35px;
  margin-bottom: var(--dl-space-space-fourunits);
}
.text03 {
  color: var(--dl-color-gray-500);
  margin-bottom: 0px;
}
.container06 {
  flex: baseline;
  width: auto;
  height: auto;
  display: flex;
  align-items: center;
  justify-content: center;
}
.container07 {
  width: 100%;
  display: flex;
  max-width: 1400px;
  align-items: stretch;
  margin-bottom: 32px;
  flex-direction: row;
  justify-content: space-between;
}
.Features {
  width: 100%;
  display: flex;
  padding-left: 48px;
  padding-right: 48px;
  padding-bottom: 48px;
  max-width: 1400px;
  flex-direction: column;
}
.text24 {
  font-size: 42px;
  margin-bottom: 48px;
}
.container10 {
  width: 100%;
  display: grid;
  grid-gap: 32px;
  grid-template-columns: 1fr 1fr;
}
.section-separator2 {
  width: 100%;
  height: 1px;
  display: flex;
  align-items: flex-start;
  flex-direction: column;
  background-color: var(--dl-color-gray-900);
}
.footer-container {
  width: 100%;
  display: flex;
  align-items: center;
  flex-direction: column;
  justify-content: center;
  background-color: var(--dl-color-gray-white);
}
.footer {
  width: 100%;
  display: flex;
  max-width: var(--dl-size-size-maxcontent);
  align-items: center;
  padding-top: var(--dl-space-space-tripleunit);
  padding-left: var(--dl-space-space-tripleunit);
  padding-right: var(--dl-space-space-tripleunit);
  flex-direction: row;
  padding-bottom: var(--dl-space-space-tripleunit);
  justify-content: space-between;
}
.social-links {
  display: flex;
  align-items: flex-start;
  flex-direction: row;
}
.icon02 {
  width: 24px;
  height: 24px;
  margin-right: var(--dl-space-space-doubleunit);
}
.icon04 {
  width: 24px;
  height: 24px;
  margin-right: var(--dl-space-space-doubleunit);
}
.icon06 {
  width: 24px;
  height: 24px;
}
.copyright-container {
  display: flex;
  align-items: center;
  flex-direction: row;
}
.icon08 {
  width: 24px;
  height: 24px;
  margin-right: var(--dl-space-space-doubleunit);
}
@media (max-width: 991px) {
  .image {
    width: 30%;
  }
  .links-container {
    width: 60%;
  }
  .Oferta {
    padding-top: var(--dl-space-space-fiveunits);
    padding-left: 32px;
    padding-right: 32px;
    padding-bottom: 0px;
  }
  .container01 {
    align-self: center;
    align-items: center;
  }
  .text24 {
    text-align: center;
    font-size: 32px;
    margin-bottom: var(--dl-space-space-fourunits);
  }
  .text02 {
    font-size: 28px;
    margin-bottom: var(--dl-space-space-tripleunit);
  }
  .text14 {
    margin-bottom: var(--dl-space-space-tripleunit);
  }
  .text22 {
    margin-bottom: var(--dl-space-space-tripleunit);
  }
  .Features {
    padding-top: var(--dl-space-space-fiveunits);
    padding-left: 32px;
    padding-right: 32px;
    padding-bottom: var(--dl-space-space-fiveunits);
  }
}
@media (max-width: 767px) {
  .image {
    width: 45%;
  }
  .links-container {
    display: none;
  }
  .burger-menu {
    display: flex;
    padding: var(--dl-space-space-halfunit);
    margin-left: var(--dl-space-space-tripleunit);
    border-radius: var(--dl-radius-radius-radius4);
    background-color: var(--dl-color-gray-black);
  }
  .icon {
    fill: var(--dl-color-gray-white);
  }
  .Oferta {
    padding-top: var(--dl-space-space-fiveunits);
    padding-left: 32px;
    padding-right: 32px;
  }
  .image1 {
    align-self: flex-start;
  }
  .container02 {
    width: 100%;
  }
  .text {
    margin-bottom: var(--dl-space-space-unit);
  }
  .text02 {
    font-size: 26px;
    margin-bottom: var(--dl-space-space-tripleunit);
  }
  .text13 {
    margin-bottom: var(--dl-space-space-unit);
  }
  .text14 {
    margin-bottom: var(--dl-space-space-tripleunit);
  }
  .Features {
    padding-top: var(--dl-space-space-tripleunit);
    padding-left: 32px;
    padding-right: 32px;
    padding-bottom: var(--dl-space-space-tripleunit);
  }
  .text24 {
    font-size: 30px;
    margin-bottom: var(--dl-space-space-tripleunit);
  }
  .container10 {
    grid-template-columns: 1fr;
  }
}
@media (max-width: 479px) {
  .image {
    width: 70%;
  }
  .burger-menu {
    margin-left: var(--dl-space-space-doubleunit);
  }
  .Oferta {
    padding-top: 32px;
    padding-left: var(--dl-space-space-unit);
    padding-right: var(--dl-space-space-unit);
    padding-bottom: 0px;
  }
  .container01 {
    flex-direction: column;
  }
  .image1 {
    width: 60%;
    align-self: center;
  }
  .text {
    margin-bottom: var(--dl-space-space-doubleunit);
    text-align: center;
  }
  .text02 {
    text-align: center;
    margin-bottom: var(--dl-space-space-doubleunit);
  }
  .text03 {
    text-align: center;
  }
  .container02 {
    align-items: center;
  }
  .text22 {
    text-align: center;
    margin-bottom: var(--dl-space-space-doubleunit);
  }
  .text23 {
    text-align: center;
  }
  .Features {
    padding-top: 0px;
    padding-left: var(--dl-space-space-unit);
    padding-right: var(--dl-space-space-unit);
    padding-bottom: 32px;
  }
  .text24 {
    font-size: 28px;
    margin-bottom: var(--dl-space-space-doubleunit);
  }
  .link4 {
    font-size: 12px;
  }
}
