  flex-direction: row;
  padding-bottom: var(--dl-space-space-tripleunit);
  justify-content: space-between;
}
.navlink {
  display: contents;
}
.image {
  width: 30%;
  height: 60%;
  object-fit: cover;
  transition: 0.3s;
  text-decoration: none;
}
.image:hover {
  filter: contrast(150%);
}
.links-container {
  width: 40%;
